.catalog-main {
  padding: 150px 0 65px;
  &__inner {
    display: flex;
    margin-top: 30px;
  }
  &__left {
    width: 265px;
    margin-right: 30px;
  }
  &__right {
    width: calc(100% - 295px);
  }
  &__filter {
    border: 1px solid #dadada;
    border-radius: 5px;
    &-show {
      background: rgba(194,194,194,0.6);
      padding: 15px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      color: #282828;
    }
    &-btn {
      align-items: center;
      cursor: pointer;
      display: none;
    }
    &-desc {
      font-size: 14px;
      line-height: 120%;
      color: #282828;
      border-bottom: 1px dotted #282828;
      span:nth-child(2) {
        display: none;
      }
    }
    &-icon {
      flex-shrink: 0;
      width: 9px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 8px;
    }
  }
  &__item {
    &-show {
      display: flex;
      padding: 10px 15px;
      background: rgba(194,194,194,0.4);
      cursor: pointer;
      i {
        flex-shrink: 0;
        width: 9px;
        margin-left: 9px;
        display: flex;
        justify-content: center;
        align-items: start;
        img {
          width: 100%;
          transition: all 0.3s;
          margin-top: 7px;
        }
      }
      span {
        font-size: 15px;
        line-height: 120%;
        font-weight: 700;
        color: #282828;
      }
    }
    &-hidden {
      flex-direction: column;
      padding: 10px 15px;
      background: rgba(194,194,194,0.2);
      display: flex;
    }
    &-hidden--active {
      display: none;
    }
    .hidden-flex {
      align-items: center;
      flex-wrap: wrap;
      flex-direction: row;
      padding-bottom: 2px;
    }
    &-check {
      position: relative;
      display: inline-block;
      &:not(:first-child) {
        margin-top: 8px;
      }
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        border: none;
        opacity: 0;
        cursor: pointer;
        &:checked ~ label:before {
          opacity: 1;
          pointer-events: all;
        }
      }
      label {
        font-size: 14px;
        line-height: 120%;
        font-weight: 500;
        color: #282828;
        padding-left: 19px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 2px;
          left: 0;
          width: 12px;
          height: 12px;
          background: #ffffff;
          border: 1px solid #dadada;
          border-radius: 2px;
        }
        &:before {
          content: '';
          position: absolute;
          z-index: 1;
          top: 46%;
          left: 2px;
          transform: translate(0, -50%);
          width: 8px;
          height: 6px;
          background-image: url("../img/checked.png");
          background-repeat: no-repeat;
          background-position: center;
          background-size: 8px 6px;
          transition: all 0.3s;
          opacity: 0;
          pointer-events: none;
        }
      }
    }
    &-check2 {
      margin-bottom: 8px;
      &:not(:first-child) {
        margin-top: 0;
      }
      &:not(:last-child) {
        margin-right: 15px;
      }
    }
    &-btns {
      padding: 20px 27px;
      border-top: 1px solid rgba(194,194,194,.4);
      background: rgba(194,194,194,.2);
    }
    &-btn {
      width: 100%;
      height: 45px;
    }
    &-btn2 {
      width: 100%;
      height: 45px;
      background-color: #cfcfcf;
      border-radius: 5px;
      margin-top: 11px;
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      color: #292929;
      &:hover {
        background-color: #aaaaaa;
      }
    }
  }
  &__title {
    padding-left: 0;
    &:after {
      display: none;
    }
  }
  &__title-mob {
    padding-left: 0;
    display: none;
    &:after {
      display: none;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    margin-top: 30px;
  }
  &__pagination {
    margin-top: 30px;
  }
  &__btn {
    width: 310px;
    height: 67px;
    background-color: #cfcfcf;
    border-radius: 5px;
    font-size: 20px;
    line-height: 120%;
    font-weight: 700;
    color: #292929;
    margin: 35px auto 0;
    display: none;
    &:hover {
      background-color: #aaaaaa;
    }
  }
}
.catalog-about {
  padding: 65px 0;
  background: rgba(194,194,194,0.15);
  &__subtitle {
    font-size: 34px;
    line-height: 120%;
    font-weight: 800;
    color: #282828;
    margin-top: 50px;
  }
  &__desc {
    margin-top: 30px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #282828;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
}

@media screen and (max-width: 1250px) {
  .catalog-main {
    padding-top: 145px;
  }
}

@media screen and (max-width: 1150px) {
  .catalog-main {
    padding-top: 170px;
    &__left {
      width: 220px;
    }
    &__right {
      width: calc(100% - 250px);
    }
  }
}

@media screen and (max-width: 992px) {
  .catalog-main {
    padding-top: 166px;
    &__cards {
      grid-template-columns: 1fr 1fr;
    }
    &__filter {
      &-title {
        font-size: 15.6px;
      }
    }
    &__item {
      &-show {
        span {
          font-size: 13px;
        }
      }
      &-check {
        label {
          font-size: 12px;
        }
      }
      &-btn {
        height: 37px;
        font-size: 13px;
      }
      &-btn2 {
        height: 37px;
        font-size: 13px;
      }
    }
  }
  .catalog-about {
    padding: 48px 0;
    &__subtitle {
      font-size: 25px;
      margin-top: 30px;
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 20px;
        }
      }
    }
  }
}

@media screen and (max-width: 835px) {
  .catalog-main {
    padding-top: 159px;
    padding-bottom: 40px;
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 20px 0 30px 0;
    }
    &__right {
      width: 100%;
    }
    &__filter {
      &-title {
        font-size: 20px;
      }
      &-btn {
        display: flex;
      }
      &-hidden {
        display: none;
      }
    }
    &__item {
      &-show {
        span {
          font-size: 15px;
        }
      }
      &-check {
        label {
          font-size: 14px;
        }
      }
    }
    &__title {
      display: none;
    }
    &__title-mob {
      display: block;
    }
    &__pagination {
      display: none;
    }
    &__btn {
      display: flex;
    }
  }
  .catalog-about {
   padding: 40px 0;
  }
}

@media screen and (max-width: 576px) {
  .catalog-main {
    &__cards {
      grid-template-columns: 1fr;
      max-width: 450px;
      margin: 20px auto 0;
    }
  }
}

@media screen and (max-width: 470px) {
  .catalog-main {
    padding-top: 144px;
    &__btn {
      width: 250px;
      height: 45px;
    }
  }
}