.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-green {
  background-color: #28920b;
  border-radius: 5px;
  font-size: 15px;
  line-height: 120%;
  font-weight: 700;
  color: #ffffff;
  &:hover {
    box-shadow: 0px 10px 16px 0px rgba(40,146,11,0.34);
  }
}
.display2 {
  font-size: 52px;
  line-height: 120%;
  font-weight: 800;
  color: #000000;
  position: relative;
  span:nth-child(1) {
    color: #28920b;
  }
  span:nth-child(2) {
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 1px rgba(194,194,194,0.49);
    opacity: 0.4;
    position: absolute;
    top: -14px;
    left: -14px;
  }
  &:after {
    content: '';
    position: absolute;
    top: 28px;
    left: -170px;
    width: 125px;
    height: 6px;
    background: #28920b;
  }
}
.up {
  position: fixed;
  z-index: 4;
  right: 20px;
  bottom: 20px;
  width: 72px;
  height: 72px;
  background-color: #28920b;
  box-shadow: 0px 10px 16px 0px rgba(39,72,29,0.34);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  img {
    max-width: 21px;
  }
  &--active {
    opacity: 1;
    pointer-events: all;
  }
}
.nav {
  padding: 50px 0;
  &__list {
    li {
      font-size: 20px;
      line-height: 120%;
      &:not(:first-child) {
        margin-top: 12px;
      }
      &:hover {
        color: #28920b;
      }
    }
  }
}
.breadcrumbs {
  padding: 17px 0 0 0;
  border-bottom: 1px solid #e5e5e5;
  &__items {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__item {
    font-size: 15px;
    line-height: 120%;
    font-weight: 500;
    color: #28920b;
    margin-bottom: 17px;
    &:not(:last-child) {
      position: relative;
      margin-right: 30px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -20px;
        transform: translate(0, -50%);
        width: 6px;
        height: 8px;
        background-image: url("../img/icon.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 6px 8px;
      }
    }
    &--active {
      color: #282828;
    }
  }
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  &__item {
    width: 48px;
    height: 48px;
    background-color: #f1f1f1;
    border: 1px solid #dcdcdc;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    line-height: 105%;
    font-weight: 600;
    color: #8f8076;
    &--active {
      background-color: #28920b;
      color: #ffffff;
    }
    &:hover {
      background-color: #28920b;
      color: #ffffff;
    }
    &:not(:first-child) {
      margin-left: 14px;
    }
  }
  &__btn--prev {
    width: 11px;
    img {
      width: 100%;
    }
  }
  &__btn--next {
    width: 11px;
    margin-left: 14px;
    img {
      width: 100%;
      transform: rotate(180deg);
    }
  }
}
.swiper-button-prev {
  z-index: 2;
  width: 44px;
  height: 44px;
  border: 1px solid #c2c2c2;
  background: #ffffff;
  border-radius: 50%;
  img:nth-child(2) {
    transform: rotate(180deg);
    display: none;
  }
  &:after {
    display: none;
  }
  &:hover {
    border: 1px solid #28920b;
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block;
    }
  }
}
.swiper-button-next {
  z-index: 2;
  width: 44px;
  height: 44px;
  border: 1px solid #c2c2c2;
  background: #ffffff;
  border-radius: 50%;
  img:nth-child(1) {
    transform: rotate(180deg);
  }
  img:nth-child(2) {
    display: none;
  }
  &:after {
    display: none;
  }
  &:hover {
    border: 1px solid #28920b;
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block;
    }
  }
}
.swiper-pagination {
  &-bullet {
    width: 10px;
    height: 10px;
    background: transparent;
    border: 2px solid #343434;
    opacity: 1;
  }
  &-bullet-active {
    background: #28920b;
    border: 2px solid transparent;
  }
}
.select__wrap {
  &--active {
    .select-arrow {
      transform: rotate(180deg);
    }
    .select__list {
      display: block;
    }
  }
}
.select__list-default {
  min-height: 52px;
  height: 100%;
  background: #28920b;
  border: 1px solid transparent;
  border-radius: 5px 5px 0px 0px;
  padding: 5px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  li {
    font-size: 20.4px;
    line-height: 120%;
    font-weight: 700;
    background: #28920b !important;
    color: #ffffff !important;
    border: none !important;
  }
}
.select-arrow {
  transition: all 0.3s;
}
.select__list {
  display: none;
}
.select__tab {
  width: 100% !important;
  height: 52px !important;
  justify-content: start !important;
  padding: 5px 20px;
  &:hover {
    background: rgba(194,194,194,0.6);
    border: 1px solid rgba(194,194,194,0.6);
    color: #262626;
  }
}
.li--active {
  background: rgba(194,194,194,0.6) !important;
  border: 1px solid rgba(194,194,194,0.6) !important;
  color: #262626 !important;
}
.vk:hover {
  background: #0077FF;
}
.telegram:hover {
  background: #27A7E7;
}
.ok:hover {
  background: #f80;
}
.youtube:hover {
  background: #FF0000;
}

@media screen and (max-width: 1500px) {
  .display2 {
    padding-left: 32px;
    &:after {
      left: -85px;
      width: 100px;
    }
    span:nth-child(2) {
      left: 18px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .display2 {
    font-size: 40px;
    &:after {
      top: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .display2 {
    font-size: 35px;
    &:after {
      top: 18px;
    }
  }
  .breadcrumbs {
    &__item {
      font-size: 13px;
    }
  }
}

@media screen and (max-width: 835px) {
  .display2 {
    font-size: 30px;
    &:after {
      top: 14px;
    }
  }
  .breadcrumbs {
    &__item {
      font-size: 15px;
    }
  }
}
