.equipment-main {
  padding: 153px 0 65px;
  &__title {
    margin-top: 40px;
    font-size: 34px;
    padding-left: 0;
    &:after {
      display: none;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 60px;
  }
  &__left {
    width: 265px;
    margin-right: 30px;
  }
  &__right {
    width: calc(100% - 395px);
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__article {
    font-size: 16px;
    line-height: 120%;
    color: #969696;
    span {
      font-weight: 500;
      color: #000000;
    }
  }
  &__price {
    font-size: 37.4px;
    line-height: 120%;
    font-weight: 800;
    color: #262626;
    margin-top: 15px;
    span {
      font-size: 28.4px;
      font-weight: 400;
    }
  }
  &__btns {
    display: flex;
    margin-top: 15px;
  }
  &__btn {
    width: 245px;
    height: 45px;
    padding-right: 50px;
    position: relative;
    span {
      position: absolute;
      top: 0;
      right: 0;
      width: 50px;
      height: 100%;
      background-color: #207a07;
      border-radius: 0px 5px 5px 0px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    span img {
      max-width: 19px;
    }
  }
  &__calc {
    display: flex;
    align-items: center;
    margin-left: 23px;
    input {
      width: 45px;
      height: 45px;
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      color: #262626;
      background: transparent;
      border: none;
      padding: 0;
      margin: 0;
      text-align: center;
    }
  }
  &__plus {
    width: 45px;
    height: 45px;
    background: #28920b;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    cursor: pointer;
  }
  &__minus {
    width: 45px;
    height: 45px;
    background: #28920b;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    cursor: pointer;
  }
  &__subtitle {
    font-size: 20px;
    line-height: 120%;
    font-weight: 700;
    color: #28920b;
    margin-top: 35px;
  }
  &__list {
    margin-top: 15px;
    li {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px dotted #262626;
      &:not(:first-child) {
        margin-top: 7px;
      }
      span:nth-child(1) {
        font-size: 16px;
        line-height: 120%;
        color: #262626;
        position: relative;
        br {
          display: none;
        }
        &:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: -3px;
          width: calc(100% + 5px);
          height: 5px;
          background: #fff;
        }
      }
      span:nth-child(2) {
        font-size: 16px;
        line-height: 120%;
        font-weight: 600;
        color: #262626;
        margin-left: 15px;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: -3px;
          width: calc(100% + 5px);
          height: 5px;
          background: #fff;
        }
      }
    }
  }
}
.simila-products {
  padding: 65px 0 115px;
  background: rgba(194,194,194,0.15);
  border-top: 1px solid rgba(194,194,194,0.49);
  &__slider {
    position: relative;
    margin-top: 40px;
  }
  .swiper-button-prev {
    left: -90px;
  }
  .swiper-button-next {
    right: -90px;
  }
  .swiper-pagination {
    bottom: -50px;
  }
}
.info2 {
  &__tabs {
    &-mob {
      display: none;
    }
    &-head {
      display: flex;
      align-items: center;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: -5000px;
        width: 10000px;
        height: 100%;
        background-color: #f1f1f1;
        pointer-events: none;
      }
    }
    &-body {
      padding: 40px 0;
    }
    &-content {
      display: none;
      &--active {
        display: block;
      }
    }
  }
  &__tab {
    width: 265px;
    height: 65px;
    border: 1px solid #dcdcdc;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    color: #262626;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: #f1f1f1;
    position: relative;
    z-index: 2;
    &--active {
      background: #28920b;
      border: 1px solid #28920b;
      color: #ffffff;
    }
  }
  &__doc {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 20px;
    }
    &-img {
      width: 44px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 18px;
    }
    &-download {
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      text-decoration: underline;
      color: #28920b;
    }
    &-subtitle {
      font-size: 14px;
      line-height: 120%;
      font-weight: 600;
      color: #262626;
      margin-top: 5px;
      span {
        font-weight: 400;
        color: #828282;
      }
    }
  }
  &__desc {
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 800;
      color: #282828;
    }
    &-text {
      margin-top: 22px;
      p {
        font-size: 15px;
        line-height: 140%;
        font-weight: 400;
        color: #282828;
      }
      p:not(:first-child) {
        margin-top: 15px;
      }
    }
    &-list {
      margin-top: 35px;
      li {
        display: flex;
      }
      li:not(:first-child) {
        margin-top: 15px;
      }
      li i {
        max-width: 17px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      li span {
        font-size: 15px;
        line-height: 120%;
        font-weight: 600;
        color: #282828;
        margin-left: 10px;
      }
    }
  }
  &__characteristic {
    li {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px dotted #262626;
      &:not(:first-child) {
        margin-top: 10px;
      }
      span:nth-child(1) {
        font-size: 16px;
        line-height: 120%;
        color: #262626;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: -3px;
          width: calc(100% + 5px);
          height: 5px;
          background: #fff;
        }
      }
      span:nth-child(2) {
        font-size: 16px;
        line-height: 120%;
        font-weight: 600;
        color: #262626;
        margin-left: 15px;
        position: relative;
        &:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: -3px;
          width: calc(100% + 5px);
          height: 5px;
          background: #fff;
        }
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .simila-products {
    padding-bottom: 65px;
    &__slider-btns {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 50px;
    }
    .swiper-button-prev {
      position: static;
    }
    .swiper-button-next {
      position: static;
    }
    .swiper-pagination {
      position: static;
      margin-top: -20px;
    }
  }
}

@media screen and (max-width: 1250px) {
  .equipment-main {
    padding-top: 145px;
  }
}

@media screen and (max-width: 1200px) {
  .equipment-main {
    &__title {
      font-size: 28px;
      margin-top: 35px;
    }
    &__inner {
      margin-top: 35px;
    }
    &__left {
      width: 215px;
      margin-right: 70px;
    }
    &__right {
      width: calc(100% - 285px);
    }
  }
}

@media screen and (max-width: 1150px) {
  .equipment-main {
    padding-top: 170px;
    padding-bottom: 45px;
    &__article {
      font-size: 13.2px;
    }
    &__price {
      font-size: 30.8px;
      span {
        font-size: 23.4px;
      }
    }
    &__btn {
      width: 203px;
      height: 38px;
      font-size: 12.4px;
      padding-right: 42px;
      span {
        width: 42px;
      }
    }
    &__minus,
    &__plus {
      width: 38px;
      height: 38px;
    }
    &__calc {
      margin-left: 18px;
      input {
        width: 38px;
        height: 38px;
        font-size: 12.4px;
      }
    }
    &__subtitle {
      font-size: 16.5px;
    }
    &__list {
      li {
        span:nth-child(1) {
          font-size: 13.2px;
        }
        span:nth-child(2) {
          font-size: 13.2px;
        }
      }
    }
  }
  .simila-products {
    padding: 45px 0;
  }
  .info2 {
    &__tab {
      width: 237px;
      height: 59px;
      font-size: 16px;
    }
    &__doc {
      &-img {
        width: 38px;
      }
      &-download {
        font-size: 14.3px;
      }
      &-subtitle {
        font-size: 12.5px;
      }
    }
    &__desc {
      &-title {
        font-size: 20px;
      }
      &-text {
        p {
          font-size: 12.5px;
        }
      }
      &-list {
        li span {
          font-size: 12.5px;
        }
      }
    }
    &__characteristic {
      li {
        span:nth-child(1) {
          font-size: 12.5px;
        }
        span:nth-child(2) {
          font-size: 12.5px;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .equipment-main {
    padding-top: 167px;
    padding-bottom: 90px;
    &__title {
      font-size: 25px;
    }
    &__left {
      width: 188px;
    }
    &__right {
      width: calc(100% - 258px);
    }
    &__list {
      li {
        align-items: flex-end;
      }
      li span:nth-child(1) br {
        display: block;
      }
    }
  }
  .simila-products {
    &__slider {
      margin-top: 25px;
    }
  }
  .info2 {
    &-tabs {
      &-body {
        padding: 30px 0;
      }
    }
    &__tab {
      width: 182px;
      height: 45px;
      font-size: 12.3px;
    }
  }
}

@media screen and (max-width: 835px) {
  .equipment-main {
    padding-top: 159px;
    padding-bottom: 40px;
    &__inner {
      display: block;
      margin-top: 40px;
    }
    &__left {
      width: 100%;
      max-width: 188px;
      margin: 0 auto 55px;
    }
    &__right {
      width: 100%;
    }
    &__title {
      font-size: 30px;
      margin-top: 25px;
    }
    &__article {
      font-size: 16.2px;
    }
    &__price {
      font-size: 37.6px;
      span {
        font-size: 28.6px;
      }
    }
    &__btn {
      width: 247px;
      height: 46px;
      font-size: 15.2px;
      padding-right: 51px;
      span {
        width: 51px;
      }
    }
    &__calc {
      input {
        width: 46px;
        height: 46px;
        font-size: 15.2px;
      }
    }
    &__plus,
    &__minus {
      width: 46px;
      height: 46px;
      font-size: 25.2px;
    }
    &__subtitle {
      font-size: 18.1px;
    }
    &__list {
      li {
        span:nth-child(1) {
          font-size: 14.5px;
        }
        span:nth-child(2) {
          font-size: 14.5px;
        }
      }
    }
  }
  .info2 {
    &__tabs {
      &-head {
        display: none;
      }
      &-mob {
        display: block;
      }
    }
    &__tab {
      font-size: 18px;
    }
    &__doc {
      &-download {
        font-size: 16px;
      }
      &-subtitle {
        font-size: 14px;
      }
    }
    &__desc {
      &-title {
        font-size: 24px;
      }
      &-text {
        p {
          font-size: 15px;
        }
      }
      &-list {
        li span {
          font-size: 15px;
        }
      }
    }
    &__characteristic {
      li {
        align-items: flex-end;
        span:nth-child(1) {
          font-size: 15px;
        }
        span:nth-child(2) {
          font-size: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 470px) {
  .equipment-main {
    padding-top: 145px;
    &__btns {
      display: block;
    }
    &__calc {
      margin: 15px 0 0 0;
    }
    &__list {
      li {
        align-items: start;
        border: none;
      }
    }
  }
  .info2 {
    &__characteristic {
      li {
        border: none;
      }
    }
  }
}