.footer {
  background-color: #0b3400;
  padding: 40px 0 40px 87px;
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    &:not(:first-child) {
      margin-left: 25px;
    }
  }
  &__logo {
    display: block;
    width: 250px;
    img {
      width: 100%;
    }
  }
  &__info {
    margin-top: 23px;
  }
  &__rights {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    color: #6c9561;
    span {
      font-weight: 400;
    }
  }
  &__brevis {
    display: flex;
    margin-top: 93px;
    &-img {
      flex-shrink: 0;
      width: 31px;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-size: 12px;
      line-height: 15px;
      color: #6c9561;
      margin-left: 12px;
      a {
        font-weight: 700;
        text-decoration: underline;
        display: block;
      }
    }
  }
  &__item-title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
  }
  &__list {
    margin-top: 23px;
    li {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 12px;
      }
      &:hover {
        color: #28920b;
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .footer {
    &__logo {
      width: 215px;
    }
    &__rights {
      font-size: 13px;
    }
    &__brevis {
      margin-top: 73px;
      &-img {
        width: 27px;
      }
      &-desc {
        font-size: 10.7px;
      }
    }
    &__item-title {
      font-size: 17px;
    }
    &__list {
      margin-top: 20px;
      li {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 835px) {
  .footer {
    padding: 40px 0 162px;
    position: relative;
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 40px;
    }
    &__item {
      &--100 {
        grid-column: 1/3;
      }
      &:not(:first-child) {
        margin-left: 0;
      }
    }
    &__logo {
      width: 250px;
    }
    &__info {
      position: absolute;
      left: 15px;
      bottom: 25px;
    }
    &__rights {
      font-size: 14px;
    }
    &__brevis {
      margin-top: 20px;
      &-img {
        width: 31px;
      }
      &-desc {
        font-size: 12px;
      }
    }
    &__item-title {
      font-size: 20px;
    }
    &__list {
      li {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 470px) {
  .footer {
    &__items {
      grid-template-columns: 1fr;
    }
    &__item {
      &--100 {
        grid-column: 1/1;
      }
    }
  }
}